<!-- 作业中心 -->
<template>
	<view class="page">
		<view class="homework-all-wrap">
			<view class="top flex align-center justify-center font-weight-bold course-tab">
				<view class="tab-item" @click="changeTab(index)" v-for="(item, index) in tabBars" :key="index" :class="tabIndex === index ? 'text-main' : 'text-light-muted'">
					{{item.name}}
				</view>
			</view>
			<swiper :current="tabIndex" class="swiper-box" :style="{height:scrollH+'px'}" @change="onChangeTab">
				<!-- 待完成 -->
				<swiper-item v-for="(item,inex) in tabBars">
					<scroll-view class="scroll-view" scroll-y :style="'height:' + scrollH+'px;'">
						<view @click="viewHomework(item.id,item.name)" class="homework-item" v-for="(item,index) in course_info">
							<view class="homework-top-wrap">
								<!-- <text class="iconfont icon-tianxiexiangqing"></text> -->
								<image class="icon" src="../../static/task_zuoye.png"></image>
								<text class="homework-title">{{item.name}}</text>
								<template v-if="tabIndex == 0">
									<text class="sub-status">未提交</text>
								</template>
								<template v-else-if="tabIndex == 1">
									<template v-if="item.isreview ==1">
										<view class="source" style="display: flex; flex-direction: column;">
											<text class="sub-status-score">{{item.score}}</text>
											<image class="sourceline" src="../../static/task_line.png"></image>
										</view>
									</template>
									<template v-else>
										<text class="sub-status">待批阅</text>
									</template>

								</template>
								<template v-else>
									<text class="sub-status">已超时</text>
								</template>
							</view>
							<view class="line"></view>
							<view class="course-title">
								<text>关联课程:</text>
								<text>{{item.coursename}}</text>
							</view>
							<view class="cut-off-time">
								<text>{{item.classname + ' ' +'|' + ' '}}</text>
								<text>提交截止时间：</text>
								<text>{{item.end_time}}</text>
							</view>
						</view>
					</scroll-view>
				</swiper-item>


			</swiper>

			<template v-if="kongkong == true">
				<view :class="{xiangziwrap : (kongkong == true)}">
					<image class="xiangzi" src="../../static/images/xiangzi.png" mode="aspectFill"></image>
					<text class="xiangzi_txt">没有相关作业</text>
				</view>
			</template>
		</view>
	</view>
</template>

<script>
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
	import courseList from '@/components/common/course-list.vue'
	const app = getApp();

	export default {
		components: {
			uniNavBar,
			courseList,
		},
		data() {
			return {
				scrollH: 500,
				tabIndex: 0,
				kongkong: false,
				tabBars: [{
					name: "待完成"
				}, {
					name: "已完成"
				}, {
					name: "已超时"
				}],
				course_info: [

				],
			}
		},
		onShow() {
			// #ifdef APP-PLUS
			plus.screen.unlockOrientation(); //解除屏幕方向的锁定，但是不一定是竖屏；
			plus.screen.lockOrientation('portrait-primary'); //锁死屏幕方向为竖屏
			// #endif
		},
		onLoad() {
			this.gettest(this.tabIndex);
		},
		methods: {
			//切换选项卡
			changeTab(index) {
				this.tabIndex = index;
				this.gettest(this.tabIndex);
			},
			//滑动
			onChangeTab(e) {
				//切换当前索引
				this.tabIndex = e.detail.current;
				this.gettest(this.tabIndex);
			},
			gettest(index) {
				var path = '';
				if (index == 0) {
					path = 'Task.GetWait';
				} else if (index == 1) {
					path = 'Task.GetComplete';
				} else if (index == 2) {
					path = 'Task.GetTimeout';
				}

				setTimeout(() => {
					uni.request({
						url: getApp().globalData.site_url + path,
						method: 'GET',
						data: {
							'uid': getApp().globalData.userinfo.id,
							'token': getApp().globalData.userinfo.token,
							'p': 1
						},
						success: res => {
							//console.log(res);
							if (res.data.data.code == 700) {
								uni.navigateTo({
									url: '../login/login',
								});
							}

							if (res.data.data.code == 0) {
								this.course_info = res.data.data.info;
								//console.log(JSON.parse(JSON.stringify(this.course_info)));
								this.kongkong = false;
								if (this.course_info.length == 0) {
									this.kongkong = true;
								}
							} else {
								this.kongkong = true;
							}
						}
					});
				}, 0);
			},

			view(taskid, name) {
				let gData = getApp().globalData;

				// 获取作业	
				uni.request({
					url: gData.site_url + 'Task.GetTask',
					method: 'GET',
					data: {
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token,
						'taskid': taskid
					},
					success: res => {
						console.log(res);
						if (res.data.data.code == 0) {
							var info = res.data.data.info[0];

							let type = 0;
							if (this.tabIndex == 0) {
								type = 0;
							} else if (this.tabIndex == 1) {
								type = 1;
							} else {
								type = 2;
							}
							uni.navigateTo({
								url: '../homework_info/homework_info?info=' + encodeURIComponent(JSON.stringify(info)) + '&type=' +
									type + '&name=' + name,
							});
						} else {
							uni.showToast({
								title: res.data.data.msg,
								icon: 'none'
							});
						}
					},

				});
			},
			// 查看家庭作业
			viewHomework(taskid, name) {

				// #ifdef MP-WEIXIN
				this.view(taskid, name);
				// #endif
				// #ifdef H5

				uni.showModal({
					title: '提示',
					content: '请打开横屏模式并横置手机，否则无法正常体验',
					showCancel: false,
					cancelText: '',
					confirmText: '确定',
					success: res => {
						this.view(taskid, name);

					},
					fail: () => {},
					complete: () => {}
				});
				// #endif



			}
		}
	}
</script>

<style>
	.page {
		width: 100%;
		height: 100%;
	}

	.source {
		width: 100rpx;
		height: 80rpx;

	}

	.sourceline {
		width: 60rpx;
		height: 20rpx;
		margin-left: 30rpx;
		margin-top: 50rpx;
	}

	.sourceline2 {
		width: 60rpx;
		height: 2rpx;
		background-color: #FF1B20;
		margin-left: 30rpx;
		margin-top: 5rpx;
	}

	.icon {
		width: 30rpx;
		height: 30rpx;
		margin-left: 10rpx;
		margin-top: 20rpx;
	}

	.line {
		position: absolute;
		right: 50rpx;
		left: 50rpx;
		height: 1rpx;
		margin-top: 10rpx;
		background-color: #E7EBED;
	}

	.homework-all-wrap {
		width: 100%;
		height: 100%;
		border: 2rpx solid #F5F5F5;
	}

	.course-tab {
		width: 100%;
		/* margin-left: -226rpx; */
	}

	.tab-item {
		margin: 0 auto;
		height: 60rpx;

		margin-top: 20rpx;
	}

	/* 作业列表 */
	.homework-item {
		height: 154rpx;
		margin-left: 20rpx;
		margin-right: 20rpx;
		margin-top: 20rpx;
		padding: 10rpx 20rpx;
		border-radius: 10rpx;
		border: 2rpx solid #F5F5F5;
	}

	.homework-top-wrap {
		height: 60rpx;
		font-size: 20rpx;
		display: flex;
		flex-direction: row;
	}

	.homework-title {
		display: inline-block;
		margin-left: 15rpx;
		width: 75%;
		font-size: small;
		font-weight: bold;
		margin-top: 10rpx;

	}

	.sub-status {
		display: inline-block;
		width: 90rpx;
		height: 40rpx;
		line-height: 40rpx;
		text-align: center;
		background-color: #FFEDED;
		font-size: 18rpx;
		border-radius: 20rpx;
		color: #FF1B20;
		position: absolute;
		right: 50rpx;
		margin-top: 10rpx;
	}

	.sub-status-score {
		display: inline-block;
		width: 90rpx;
		height: 40rpx;
		line-height: 40rpx;
		text-align: center;
		/* background-color: #FFEDED; */
		font-size: large;
		font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
		border-radius: 20rpx;
		color: #FF1B20;
		position: absolute;
		right: 50rpx;
		margin-top: 10rpx;

	}

	/* 关联课程 */
	.course-title {
		margin-top: 20rpx;
		color: #000000;
		font-size: 22rpx;
		-webkit-line-clamp: 1;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		overflow: hidden;
		margin-left: 10rpx;

	}

	.cut-off-time {
		margin-top: 10rpx;
		font-size: 18rpx;
		color: #969696;
		margin-left: 10rpx;
	}

	.xiangziwrap {
		position: absolute;
		left: calc(50% - 80px);
		top: calc(50% - 100px);
		width: 300rpx;
		height: 100rpx;
	}
	.xiangzi {
		margin-left: 100rpx;
		width: 100rpx;
		height: 100rpx;
	}
	.xiangzi_txt {
		width: 100%;
		display: block;
		text-align: center;
		font-size: 18rpx;
		color: #C7C7C7;
	}
</style>
